body {
  /* background-color: #f783e3; */
  font-family: "Raleway", sans-serif;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 68px;
  color: #fff;
  text-align: center;
}

h2 {
  color: #fff;
  margin: 10px;
  padding-right: 10px;
  text-align: center;
}

p {
  font-size: 10px;
  color: #fff;
}

.wrapper {
  padding: 0 auto;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.backgroundImage {
  position: fixed;
}

.backgroundImage img {
  height: 100vh;
  width: 100vw;
  -o-object-fit: cover;
     object-fit: cover;
}

.header {
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: baseline;
      align-items: baseline;
  padding: 20px 0;
  width: 1500px;
  z-index: 10;
}

#SR {
  padding-right: 50px;
}

#radio {
  width: 100px;
  height: 100px;
}

.station {
  width: 300px;
  height: 500px;
  margin: 20px;
  padding-bottom: 40px;
  border: 6px solid #fff;
  border-radius: 20px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.stationImage img {
  border-radius: 20px;
  width: 100%;
}

.stationTagline {
  padding:10px;
}

.stationAudio {
  width: 200px;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  bottom: 10px;
}

@media (max-width: 900px) {
  .header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }

  #SR {
    padding-right: 0;
  }
}

@media (max-width: 600px) {
  .station {
    margin: 20px;
  }
}



